.announcement-detail .announcement-head {
  padding: 0 30px;
  background: #fff;
  color: #2A283D;
  border-radius: 5px;
  line-height: 60px;
}
.announcement-detail .announcement-head span {
  font-size: 18px;
}
.announcement-detail .announcement-content {
  width: 1040px;
  min-height: calc(100vh - 140px);
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 3px #eee;
  margin: 48px auto 0;
  padding: 10px 20px 30px;
}
.announcement-detail .announcement-content > div:first-child {
  text-align: center;
  margin: 40px 0 25px;
}
.announcement-detail .announcement-content div:last-child {
  padding: 0 80px;
}
.noticeContent-look-notice {
  font-size: 18px;
  font-weight: 500;
  color: #000000;
  position: relative;
  left: -2%;
}
.noticeContent-title {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
}
.noticeContent-content {
  font-family: Alibaba PuHuiTi;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 38px;
}
